<template>
  <div class="p-4">
    <form @submit.prevent="submit">
      <div class="card m-4">
        <div class="card-header">Quittez mon Espace</div>
        <div class="card-body">
          <div class="text-center text-warning" v-if="errorMessage">
            {{ errorMessage }}
            <hr />
            <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
          </div>
          <loading-compoenent v-else-if="isLoading" />
          <div v-else>Quittez mon Espace ....</div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { auth } from "../firebase";

export default {
  name: "signOutComponent",
  components: {},
  data() {
    return {
      user: {
        email: "",
        password: ""
      },
      errorMessage: null,
      isLoading: false
    };
  },
  created() {
    this.isLoading = true;
    this.errorMessage = null;
    auth
      .signOut()
      .then(() => {
        this.isLoading = false;
        this.errorMessage = null;
        this.$swal({
          title: "Bye!",
          text: "You have loged out",
          icon: "info",
          confirmButtonText: "Okay!"
        }).then(() => {});
      })
      .catch(error => {
        this.isLoading = false;
        this.errorMessage = error;
        console.log("error", error);
      });
  },
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
    }
  }
};
</script>

